<template>
	<div class="d-box v-box">
		<div class="headDiv">
			<div class="headerLeft">
				<span class="spanclass">创建时间</span>
				<el-date-picker class="timeclass" v-model="timeValue" type="daterange" start-placeholder="开始日期" end-placeholder="结束日期" :default-time="['00:00:00', '23:59:59']" value-format="yyyy-MM-dd HH:mm:ss">
				</el-date-picker>
			</div>
			<div class="clearfix">
				<div class="headRight">
					<SolidButton :type='"solid"' :text='"查询"' @click="queryClick"></SolidButton>
					<SolidButton :type='"hollow"' :text='"重置"' @click="resetClick"></SolidButton>
				</div>
			</div>
		</div>

		<div class="flexTable">
			<div class="d-box v-box">
				<div class="flexTable spaceTable">
					<div class="tableDiv">
						<el-table v-loading="loading" ref="multipleTable" stripe :data="rows" tooltip-effect="dark" style="width: 100%;" height="100%" @selection-change="handleSelectionChange" @row-click="toggleSelection">
							<el-table-column label="标题" prop="title" align="center">
								<template slot-scope="scope">
									<el-button class="clickStyle" type="text" size="medium" @click="dateClick(scope.row)">{{scope.row.title?scope.row.title:'无'}}
									</el-button>
								</template>
							</el-table-column>
							<el-table-column prop="dealerName" label="所属主体" width="250" align="center">
							</el-table-column>
							<el-table-column label="类型" align="center">
								<template slot-scope="scope">
									<span>{{scope.row.type | typeMethod}}</span>
								</template>
							</el-table-column>
							<el-table-column label="提交时间" align="center">
								<template slot-scope="scope">
									<span>{{scope.row.createTime.slice(0,10)}}</span>
								</template>
							</el-table-column>
							<el-table-column label="处理状态" align="center">
								<template slot-scope="scope">
									<div :class="(scope.row.ishandle == 0)?'undeal-stateDiv':'deal-stateDiv'">
										<span class="dealState">{{scope.row.ishandle | ishandleMethod}}</span>
									</div>

								</template>
							</el-table-column>
						</el-table>
					</div>

				</div>
				<div class="footerDiv">
					<el-pagination style="text-align: center;padding: 6px 0px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="pageRows" layout="total,sizes, prev,pager, next,jumper" :total="pageTotal">
					</el-pagination>
				</div>
			</div>
		</div>

		<!-- 5、弹窗-->
		<el-dialog title="详情" :visible.sync="centerDialogVisible" width="600px" center>
			<el-form class="clearfix" :model="windowData">
				<div class="detailMaim">
					<div class="detailMessage">
						<div class="detailLongRow">
							<span>标题:</span>
							<span>{{windowData.title?windowData.title:'无'}}</span>
						</div>
					</div>
					<div class="detailMessage">
						<div class="detailLongRow">
							<span>所属主体:</span>
							<span>{{windowData.dealerName}}</span>
						</div>
					</div>
					<div class="detailMessage">
						<div class="detailRow">
							<span>提交人:</span>
							<span>{{windowData.custName}}</span></div>
						<div class="detailRow">
							<span>{{windowData.phone?'电话':'邮箱'}}</span>
							<span>{{windowData.phone?windowData.phone:windowData.email}}</span>
						</div>
					</div>

					<div class="detailMessage">
						<div class="detailRow">
							<span>类型:</span>
							<span>{{windowData.type | typeMethod}}</span></div>
						<div class="detailRow">
							<span>提交时间:</span>
							<span>{{windowData.Time2}}</span>
						</div>
					</div>

					<div class="detailRows detailMessage">
						<span >车辆VIN:</span>
						<el-input :readonly="true" type="textarea" resize="none" readonly="readonly" class="m_textarea" :autosize="{ minRows: 1, maxRows: 3}" v-model="windowData.vin">
						</el-input>
					</div>

					<div class="detailRows detailMessage">
						<span>内容:</span>
						<el-input :readonly="true" type="textarea" resize="none" readonly="readonly" class="m_textarea" :autosize="{ minRows: 1, maxRows: 3}" v-model="windowData.contents">
						</el-input>
					</div>

					<div class="detailRows detailMessage">
						<span>反馈信息:</span>
						<el-input :readonly="pop.isDisabled" type="textarea" resize="none" class="m_textarea" :autosize="{ minRows: 1, maxRows: 2}" placeholder="请输入反馈信息" v-model="feedback">
						</el-input>
					</div>
				</div>
			</el-form>
			<div slot="footer" class="dialog-footer" :style="{display:pop.isHidden}">
				<el-button type="primary" @click="clickConfirm" class="submitStyle">提 交</el-button>
				<el-button @click="centerDialogVisible = false" class="cancelStyle">取 消</el-button>
			</div>
		</el-dialog>
	</div>
</template>

<script>
	import SolidButton from '../../SelfControls/solidButton/solidButton.vue';
	export default {
		components: {
			SolidButton,
		},
		data() {
			return {
				//1、head头部
				timeValue: "",
				//2、main内容
				loading:true,
				rows: [],
				multipleSelection: [],
				//3、底部分页
				currentPage: 1, //当前页数
				pageRows: 10, //每页
				pageTotal: 0, //总数
				isHidden: true,
				//4、弹窗
				centerDialogVisible: false,
				windowData: "",
				feedback: '',
				pop: {
					isHidden: "none",
					isDisabled: true
				}
			}
		},
		filters: {
			typeMethod: function(value) {
				if(value == 0) {
					return '投诉'
				} else if(value == 1) {
					return '建议'
				} else {
					return '其他'
				}
			},
			ishandleMethod: function(value) {
				if(value == 0) {
					return '待处理'
				} else {
					return '处理完成'
				}
			}
		},
		methods: {
			loadData() {
				var self = this;
				var p = {
					pageNum: self.currentPage,
					pageRows: self.pageRows,
					startTime: self.timeValue[0],
					endTime: self.timeValue[1]
				}
				//1、列表接口
				self.$api.post('/crm/comment/queryList', p, r => {
					self.loading = false;
					self.rows = r.repData.list
					self.pageTotal = r.repData.total
				}, e => {
					self.loading = false;
				})
			},
			//1、head头部
			// 查询
			queryClick() {
				var self = this;
				//时间删除之后self.timeValue不存在不能用self.timeValue[0]，要给一个空值
				if(!self.timeValue) {
					self.timeValue = ""
				}
				self.loadData()
			},
			// 重置
			resetClick() {
				var self = this;
				self.timeValue = "";
				self.loadData()
			},
			//2\列表main内容
			// 点击某一列
			dateClick(row) {
				var self = this;
				console.log(row);
				//待处理（显示，可编辑）
				if(row.ishandle == 0) {
					self.pop.isHidden = "block";
					self.pop.isDisabled = false
				} else {
					console.log('已处理')
					self.pop.isHidden = "none";
					self.pop.isDisabled = true
				}
				self.windowData = row //窗口数据dic
				row.Time2 = row.createTime.substring(0, 10) //截取时间字段
				self.feedback = row.reply
				self.centerDialogVisible = true;
			},
			// 点击行选中复选框
			toggleSelection(row) {
				// var rows = [];
				// rows.push(row);
				// if (rows) {
				//   rows.forEach(row => {
				//     this.$refs.multipleTable.toggleRowSelection(row);
				//   });
				// } else {
				//   this.$refs.multipleTable.clearSelection();
				// }
			},
			// 复选框选中的数据
			handleSelectionChange(val) {
				this.multipleSelection = val;
				console.log(val);
			},
			//3、窗口
			// 点击提交按钮
			clickConfirm() {
				var self = this;
				console.log(self.windowData.id)
				var p = {
					reply: self.feedback,
					id: self.windowData.id,
					ishandle: 1
				}
				//1、接口-更新
				self.$api.post('/crm/comment/insertOrUpdate', p, r => {
					console.log(r);
					self.rows = r.repData.list
					self.centerDialogVisible = false
					this.$message({
						message: '处理成功',
						type: 'success'
					});
					self.loadData()
				}, e => {
					self.$message.error('fe');
					console.log(e);
				})
			},

			//4、footer分页
			//每页多少条数据
			handleSizeChange(val) {
				var self = this;
				console.log("每页" + val);
				self.pageRows = val;
				self.loadData();
			},
			//当前第几页
			handleCurrentChange(val) {
				var self = this;
				console.log("当前页" + val);
				self.currentPage = val;
				self.loadData();
			}
		},
		created: function() {
			this.loadData();
		}
	}
</script>

<style scoped="scoped">
	/*整体flex布局*/
	
	.d-box {
		display: -moz-box;
		/* OLD - Firefox 19- (buggy but mostly works) */
		display: -webkit-box;
		/* OLD - iOS 6-, Safari 3.1-6 */
		display: -webkit-flex;
		/* NEW - Chrome */
		display: -ms-flexbox;
		/* TWEENER - IE 10 */
		display: box;
		display: flexbox;
		display: flex;
		/* NEW, Spec - Opera 12.1, Firefox 20+ */
		height: 100%;
		width: 100%;
	}
	
	.v-box {
		-webkit-box-orient: vertical;
		-webkit-box-direction: normal;
		-moz-box-orient: vertical;
		-moz-box-direction: normal;
		-webkit-flex-direction: column;
		-moz-flex-direction: column;
		-ms-flex-direction: column;
		-o-flex-direction: column;
		flex-direction: column;
	}
	
	.flexTable {
		-webkit-box-flex: 1;
		-moz-box-flex: 1;
		-ms-flex: 1;
		flex: 1;
		position: relative;
	}
	
	.headDiv {
		padding: 20px;
		background: #fff;
		overflow: hidden;
		position: relative;
	}
	
	.tableDiv {
		width: 100%;
		height: 100%;
		position: absolute;
		overflow: auto;
	}
	
	.footerDiv {
		height: 40px;
		position: relative;
	}
	/*head头部*/	
	/*head-left内容*/
	
	.headerLeft {
		width: auto;
		height: 40px;
		float: left;
		margin-right: 20px;
	}
	
	.spanclass {
		display: inherit;
		line-height: 40px;
		margin-right: 8px;
		float: left;
	}
	
	.timeclass{
		width:432px;
		height:40px;
	}
	/*head-right内容*/
	
	.headRight {
		float: right;
	}
	/*未整理的*/
	
	.clickStyle {
		color: #418BCA;
	}
	/*半行的*/
	
	.detailRow {
		display: inline-block;
		width: 48%;
	}
	
	.detailRow span:first-of-type {
		width: 70px;
		display: inline-block;
	}
	/*独占一行的*/
	
	.detailLongRow {
		display: inline-block;
	}
	
	.detailLongRow span:first-of-type {
		width: 70px;
		display: inline-block;
	}
	
	.detailRows span:first-of-type {
		width: 70px;
		display: inline-block;
		vertical-align: top;
/*		padding-top: 2px;
*/	}
	/*长文本*/
	
	.m_textarea {
		width: 470px;
		border: none;
	}
	
	.detailMessage {
		padding: 6px 0px;
	}
	/*待处理*/
	.undeal-stateDiv {
		border-radius: 4px;
		width: 80px;
		height: 28px;
		background: #F4516B;
		margin: 0 auto;
	}
	/*处理完成*/
	.deal-stateDiv {
		border-radius: 4px;
		width: 80px;
		height: 28px;
		background: #716ACA;
		margin: 0 auto;
	}
	
	.dealState {
		font-size: 12px;
		color: white;
		line-height: 28px;
	}
	
	.submitStyle {
		width: 84px;
		height: 37px;
		background: #E05D6F;
		border-radius: 15px;
		border: #E05D6F;
	}
	
	.submitStyle:hover {
		border-color: #E05D6F;
	}
	
	.cancelStyle {
		width: 84px;
		height: 37px;
		border: 1px solid #F4516B;
		border-radius: 15px;
		color: #F4516B;
	}
	
	.cancelStyle:hover {
		background-color: #ffffff;
	}
</style>


